@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lemonada:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2&display=swap);
body {
  margin: 0;
  color: Black;
  font-size: 10px;
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
  overflow-x: hidden;
  /* width: 100vw; */
  max-width: 100%;
}

.header-container {
  display: flex;
  justify-content: space-around;
  position: fixed;
  width: 100vw;
  max-width: 100%;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  z-index: 1;
  outline: 0.07rem solid black;
}
.heading-name {
  text-transform: uppercase;
  font-size: 1.5vw;
}
.heading {
  text-transform: uppercase;
  position: relative;
  margin: 1vw 20vw;
}
.nav-bar {
  align-self: center;
}

.nav-bar > a {
  text-decoration: none;
  padding: 0 1rem;
  font-size: 1.1rem;
  color: black;
}
.nav-bar > a:hover {
  color: crimson;
}

.cover-page {
  width: 100vw;
  max-width: 100%;
  display: block;
  height: 90vh;
  background: url(/MyPortfolio/static/media/computer-table.a9552134.jpeg) no-repeat center center;
  background-size: cover;
  background-color: rgba(32, 32, 32, 0.45);
  background-blend-mode: color-burn;
}
.introduction {
  position: absolute;
  top: 28%;
  left: 12%;
  color: white;
  font-size: 1rem;
  font-weight: 200;
  font-family: "Lemonada", cursive;
  line-height: 1.3rem;
}

.media-links {
  position: fixed;
  top: 12rem;
  right: 0;
  border-radius: 0.25rem;
  display: flex;
  flex-direction: column;
  background-color: white;
  opacity: 0.7;
  outline: 0.07rem solid black;
  -webkit-animation: 1.5s ease-in 0s 1 slideInFromTop;
          animation: 1.5s ease-in 0s 1 slideInFromTop;
}
.media-links:hover {
  opacity: 0.9;
}
#profile-link {
  margin: 0.2rem;
  padding: 0.3rem;
}
#about-container {
  margin: 2.2vw 0;
}

.about-content {
  display: flex;
  font-family: "Baloo Paaji 2", cursive;
  font-size: 200%;
  line-height: 2rem;
  margin: 0 17.5vw;
}
.profile-img {
  width: 12rem;
  height: 13rem;
}
.about-info {
  padding: 1vw 3vw;
}
#resume {
  text-decoration: none;
  color: white;
  border: 0.1rem solid white;
  padding: 0 0.7rem;
  box-shadow: 0.3rem 0.3rem 1rem black;
  line-height: 3rem;
  border-radius: 3%;
}
#resume:hover {
  background-color: rgb(102, 97, 97);
}
#contact {
  display: flex;
  color: white;
  justify-content: space-between;
  outline: 0.1 solid black;
  font-size: 150%;
  padding: 0 2vw;

  max-width: 100%;
  background-color: grey;
}
@-webkit-keyframes slideInFromTop {
  0% {
    transform: translateY(-200%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slideInFromTop {
  0% {
    transform: translateY(-200%);
  }
  100% {
    transform: translateY(0);
  }
}
@media only screen and (max-device-width: 600px) {
  .heading-name {
    font-size: 4vw;
  }
  .heading {
    font-size: 4.5vw;
    padding: 1vw 0vw;
  }
  .nav-bar > a {
    font-size: 0vw;
  }
  .nav-bar > a:hover {
    font-size: 0vw;
  }
  .introduction {
    font-size: 0.7rem;
    left: 2vw;
  }
  .media-links {
    left: 0;
    right: auto;
    top: 14vw;
    margin-top: 6vw;
    flex-direction: row;
  }

  .about-content {
    display: flex;
    flex-direction: column;
  }
  .profile-img {
    width: 15rem;
    height: 15rem;
  }
  .about-info {
    margin: 0;
    padding: 0;
    font-size: 6vw;
    line-height: 8.5vw;
  }

  #contact {
    font-size: 3vw;
  }
}
@media only screen and (max-device-width: 340px) {
  .heading-name {
    font-size: 3vw;
  }
}

.projects {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  padding: 0 20vw;
}
.project {
  flex: 1 0 50%;
  padding: 0.2% 0;
}
.project:first-child {
  flex: 1 0 100%;
  align-self: center;
}
.landscape-image {
  background-image: url(/MyPortfolio/static/media/shopResize.3f87a661.gif);
  width: 825px;
  height: 422px;
}
.project-name {
  padding: 0 0.8vw;
  font-weight: 800;
  font-size: 1rem;
  text-transform: uppercase;
}

.github {
  text-decoration: none;
  color: black;
  border: 0.1rem solid white;
  padding: 0.2vw 4vw;
  margin: 0 0.4vw;
  box-shadow: 0.2rem 0.15rem 0.4rem black;
  line-height: 2.2rem;
  font-size: 1rem;
  border-radius: 3%;
}
.github:hover {
  font-weight: 800;
  font-size: 1.05rem;
}
.tech {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.3rem;
  font-size: 1rem;
}
.project-info {
  padding-right: 3vw;
  line-height: 1.5rem;
  font-size: 1rem;
}
@media only screen and (max-device-width: 824px) {
  .project {
    padding: 0vw;
  }
  .landscape-image {
    background-image: url(/MyPortfolio/static/media/shopPhone.59382976.gif);
    height: 230px;
    width: 350px;
  }
}
@media only screen and (max-device-width: 340px) {
  .landscape-image {
    width: auto;
  }
}

