@import url("https://fonts.googleapis.com/css2?family=Baloo+Paaji+2&display=swap");

.projects {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  padding: 0 20vw;
}
.project {
  flex: 1 0 50%;
  padding: 0.2% 0;
}
.project:first-child {
  flex: 1 0 100%;
  align-self: center;
}
.landscape-image {
  background-image: url("./images/shopResize.gif");
  width: 825px;
  height: 422px;
}
.project-name {
  padding: 0 0.8vw;
  font-weight: 800;
  font-size: 1rem;
  text-transform: uppercase;
}

.github {
  text-decoration: none;
  color: black;
  border: 0.1rem solid white;
  padding: 0.2vw 4vw;
  margin: 0 0.4vw;
  box-shadow: 0.2rem 0.15rem 0.4rem black;
  line-height: 2.2rem;
  font-size: 1rem;
  border-radius: 3%;
}
.github:hover {
  font-weight: 800;
  font-size: 1.05rem;
}
.tech {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.3rem;
  font-size: 1rem;
}
.project-info {
  padding-right: 3vw;
  line-height: 1.5rem;
  font-size: 1rem;
}
@media only screen and (max-device-width: 824px) {
  .project {
    padding: 0vw;
  }
  .landscape-image {
    background-image: url("./images/shopPhone.gif");
    height: 230px;
    width: 350px;
  }
}
@media only screen and (max-device-width: 340px) {
  .landscape-image {
    width: auto;
  }
}
